.navbar {
  position: fixed;
  top: 0;
  width: 100vw;
  height: 9vh;
  color: black;
  background: linear-gradient(30deg, #FAF9F6 20.3%,  #209B47 79.6%); /* Set the background color the linear color */
  font-weight: 500;
  display: flex;
  min-height: 0vh;
  overflow: hidden;
  transition: transform 0.3s ease-in-out, box-shadow 0.3s ease-in-out; /* Transitions for sliding and background color animations */
  
}

.slide-up {
  transform: translateY(-100%); /* Slide the navbar up and off the screen */
}
.visible {
  transform: translateY(0%); /* Slide the navbar down and on the screen */
}
.navbar-content {
  display: flex;
  justify-content: space-between; /* Align logo and button to opposite ends */
  align-items: center;
  padding: 0 10px; /* Adjust padding as needed */
  width: 100vw;
}

.logo-container {
  display: flex;
  align-items: center;
  margin: 0;
  position: fixed;
  z-index: 8;
  padding: 10px;
}

.logo {
  height: 50px;
  width: auto;
  margin:0;
}

.logo-text {
  margin: 0;
  font-size: 2.5rem;
  color: black;
}

.desktop-menu {
  font-size: 1.5rem;
  display: flex;
  align-items: center; /* Vertically align menu items */
  gap: 20px; /* Add spacing between menu items */
  margin-left: 89%;
}

.desktop-menu a {
  position: relative;
  color: white;
  font-weight: 400;
  text-shadow: none;
  text-decoration: none;
}

.desktop-menu a.signup {
  display: inline-block;
  padding: 6px 12px; /* Adjust padding as needed */
  border: 1px solid white; /* White outline */
  border-radius: 4px; /* Rounded corners */
  transition: background-color 0.3s ease-in-out, color 0.3s ease-in-out;
}
.desktop-menu a.signup:hover {
  border: 1px solid white;
  background-color: white;
  color: #209B47;
  cursor: pointer;
}

@media only screen and (max-width: 800px) and (max-height: 1000px) /* Phones */{
    .navbar {
      height: 6vh;

    }
    .logo-container {
      display: flex;
      align-items: center;
      margin: 0;
      padding-top: 5px;
      padding-left: 0px;
    }
    
    .logo {
      height: 10px;
      width: auto;
      margin:0;
    }
    
    .logo-text {
      margin: 0;
      font-size: 1rem;
    }
    .desktop-menu {
      display: none; /* Hide desktop menu by default on mobile */
    }   
    

  }